import { getDoc, doc } from 'firebase/firestore';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faRoute, faCalendar, faCircleInfo, faFlagCheckered, faClock, faCircleExclamation, faMap, faServer, faLocationDot } from '@fortawesome/free-solid-svg-icons'
import { db } from "../firebase-config";
import React, { useState, useEffect } from 'react'

function convertToDate(x) {
    let t = new Date(1970, 0, 1);
    t.setSeconds(x);
    return t
}

function addOrdinals(x) {
    switch (x) {
        case 1:
            return ''+x+'st';
        case 2:
            return ''+x+'nd';
        case 3:
            return ''+x+'rd';
        default:
            return ''+x+'th';
    }
}

function FetchInfoMain() {
    const [Convoy, setConvoy] = useState([]);
    const [EventTime, setEventTime] = useState({});
    
    const convoyData = async () => {
        const docRef = doc(db, "info", "convoy");
        const docSnap = await getDoc(docRef);
        let sup = document.createElement('sup');

        if (docSnap.exists()) {
            console.log("Document data:", docSnap.data());
            setConvoy(docSnap.data());
            let t = convertToDate(docSnap.data().date.seconds);
            // switch (convertToDate(docSnap.data().date.seconds).getUTCDate()) {
            //     case 1:
            //         sup.textContent = 'st';
            //         break;
            //     case 2:
            //         sup.textContent = 'nd';
            //         break;
            //     case 3:
            //         sup.textContent = 'rd';
            //         break;
            //     default:
            //         sup.textContent = 'th';
            //         break;
            // }
            let mapka =
                {
                    onlyDay: t.getUTCDate(),
                    dateD: t.toLocaleString('en-us', { month: 'long' }) + ' ' + t.getUTCFullYear(),
                time: (t.getUTCHours()<10?'0':'') + t.getUTCHours() + ':' + (t.getUTCMinutes()<10?'0':'') + t.getUTCMinutes()};
            setEventTime(mapka);
            console.log(mapka);
          }
    };

    useEffect(() => {
      convoyData();
    }, []);
    return (
        <>
        <div className='cInformations'>
            <div>
            <FontAwesomeIcon icon={faCircleInfo} />
            <span>{Convoy.title}</span>
            </div>
                <div>
            <FontAwesomeIcon icon={faCalendar} />
            <span>Date: </span>
            <span>{addOrdinals(EventTime.onlyDay) + ' ' + EventTime.dateD}</span>
            </div>
            <div>
            <FontAwesomeIcon icon={faClock} />
            <span>Meet-up: </span>
            <span>{EventTime.time} UTC</span>
            </div>
             <div>
            <FontAwesomeIcon icon={faLocationDot} />
            <span>From: </span>
            <span>{Convoy.start}</span>
            </div>
            <div>
            <FontAwesomeIcon icon={faFlagCheckered} />
            <span>Destination: </span>
            <span>{Convoy.destination}</span>
            </div>
            <div>
            <FontAwesomeIcon icon={faRoute} />
            <span>Distance: </span>
            <span>{Convoy.distance} km</span>
            </div>
             <div>
            <FontAwesomeIcon icon={faServer} />
            <span>Server: </span>
            <span>{Convoy.server}</span>
            </div>
            <div>
            <FontAwesomeIcon icon={faCircleExclamation} />
            <span>Required DLCs: </span>
            {/*Convoy.dlcs
            .map((v, i) => {
                if (i == Convoy.dlcs.lenght) {
                    
                }
            })
            
            .reduce(function(a,b){
               return a.concat(b).concat(", ");
            },[]).slice(0,-1)
            
            */}
            <span>{Convoy.dlcs}</span>
            </div>
        </div>
        <div className='cMap'>
            <FontAwesomeIcon icon={faMap} />
            <span>Map</span>
            <img src={Convoy.mapURL} alt=" could not be loaded." />
            </div>
        </>
    )
}
export default FetchInfoMain;
